.weekly {
	grid-row: 2;
	grid-column: 2 / span 3;
	display: flex;
	justify-content: space-around;
	align-items: center;
	&_day {
		width: 80px;
		height: 90%;
		position: relative;
		padding: 10px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
		border-radius: 3px;
		background-color: var(--var-tertiary-color);
		cursor: pointer;
		&::before {
			content: "";
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			opacity: 0;
			transition: 0.2s ease-in;
			border-radius: 3px;
			background: linear-gradient(
				141deg,
				rgba(187, 193, 248, 1) 27%,
				rgba(110, 218, 243, 1) 77%,
				rgba(0, 255, 235, 1) 100%
			);
		}
		&:hover {
			&::before {
				opacity: 1;
			}
		}

		&_description {
			display: flex;
			flex-direction: column;
			align-items: center;
			span {
				height: 50px;
				display: flex;
				align-items: center;
				text-align: center;
				opacity: 0.5;
			}
		}
	}
}
.index {
	z-index: 999;
}
